////////// Popup
.mfp
  position: relative
  max-width: 546px
  min-height: 463px
  text-align: left
  padding: 114px 80px
  margin: 2.5rem auto
  background: $white
  +md
    max-width: 450px
  &-bg
    background: rgba($black, 0.5) !important
    opacity: 0.9 !important

  .mfp-close
    display: flex
    justify-content: center
    align-items: center
    top: 22px
    right: 22px
    opacity: 1
    transition: all .3s ease
    width: 12px
    height: 12px
    // color: $c1
    svg
      // fill: $c1
      width: 100%
      height: 100%
      pointer-events: none
      transition: all 0.3s ease
    &:focus
      outline: none
    &:hover
      svg
        // fill: rgba($c4, .8)
    &:active
      transform: translateY(1px)

.form-result
  position: absolute
  top: 0
  left: 0
  width: 100%
  height: 100%
  display: flex
  align-items: center
  justify-content: center
  text-align: center
  background: $white
  opacity: 0
  padding: 50px
  visibility: hidden
  transition: all .3s ease
  z-index: 1
  .mess
    &__title
      font-size: 42px
      line-height: 47px
      font-weight: 600
      margin-bottom: 10px
      opacity: 0
      transform: translateY(50px)
      transition: all .6s ease
      +sm
        font-size: 30px
      +xsm
        font-size: 25px
        margin-bottom: 5px

    &__desc
      font-size: 22px
      line-height: 27px
      opacity: 0
      transform: translateY(50px)
      transition: all .6s ease
      +xsm
        font-size: 16px

  // &--success
  &--success
    opacity: 1
    visibility: visible
    .mess__title,
    .mess__desc
      opacity: 1
      transform: translateY(0)
      transition-delay: .1s
    .mess__title
      transition-delay: .2s
    .mess__desc
      transition-delay: .3s
